.container {
  width: 80vw;
  margin: auto;
  display: flex;
  flex-direction: column;
  font-size: 2.5vmin;
}

.chat-box {
  width: 100%;
  height: 80vh;
  display: inherit;
  overflow-y: auto;
  flex-direction: column-reverse;
  position: relative;
  border: 3px solid black;
  border-radius: 16px 0px 0px 16px;
}

.form {
  display: flex;
  flex-direction: row;
}
.form > * {
  flex: 2 1 auto;
}

.input, .button {
  font-size: 2.5vh;
}
